.input {
    box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.25);
    height: 35px;
    width: 300px;
    border-radius: 20px;
    border: #ebe8e8;
    padding-left: 20px;
    font-family: "Rubik", sans-serif;
    margin-left: 585px;
}

.input:focus {
    outline: none;
}

.search {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin-right: 550px;
}

.searchButton {
    background-color: #adadd0d2;
    box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    padding: 3px;
    height: 35px;
    width: 35px;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.searchButton:hover {
    background-color: #eb544d;
}

.buscar {
    height: 25px;
}